import { useState, memo } from "react"
import './Select_module.css'

const Pay = memo((props) => {
    const { number, type, charge, count, type2, deletePayInput } = props
    const [open, setOpen] = useState(true)


    const deleteThisPay = () => {
        setOpen(false)
        deletePayInput(number)
    }

    /**
     * <option value="" hidden disabled selected></option>
                                <option value="house">住宅ローン　家賃　等</option>
                                <option value="house2">住まい関連の費用（管理費など)</option>
                                <option value="sisanTax">固定資産税</option>
                                <option value="car">車やその他（ローン）</option>
                                <option value="seimeiHoken">生命保険</option>
                                <option value="study">学費など</option>
                                <option value="living">65歳までの月々の生活費</option>
                                <option value="living65">65歳以降の月々の生活費</option>
                                <option value="other">その他の支出</option>
     */

    let typeLabel1;
    if (type === "house") {
        typeLabel1 = "住宅ローン、家賃"
    } else if (type === "car") {
        typeLabel1 = "車（ローン）"
    } else if (type === "nomal") {
        typeLabel1 = "一般支出"
    } else if (type === "sisanTax") {
        typeLabel1 = "固定資産税"
    } else if (type === "seimeiHoken") {
        typeLabel1 = "生命保険"
    } else if (type === "study") {
        typeLabel1 = "学費など"
    } else if (type === "other") {
        typeLabel1 = "その他の支出"
    } else if (type === "living") {
        typeLabel1 = "65歳までの月々の生活費"
    } else {
        typeLabel1 = "65歳以降の月々の生活費"
    }

    let typeLabel2;
    if (type2 === "every") {
        typeLabel2 = "毎年"
    } else if (type2 === "before") {
        typeLabel2 = "まで毎年"
    } else if (type2 === "after") {
        typeLabel2 = "から毎年"
    } else {
        typeLabel2 = "の時に1回限り"
    }

    return (
        <>
            {open ?
                <div className="option-wrapper">
                    <br />
                    <label>種類 : {typeLabel1}</label>
                    <br />
                    <label>金額（年額） : {charge}万円</label>
                    <br />
                    {type2 === "every" ?
                        <label>期間 : {typeLabel2}</label> :
                        <label>期間 : {count}歳{typeLabel2}</label>
                    }
                    <button className="btn btn-border" onClick={deleteThisPay}>削除</button>
                </div>
                : null}

        </>
    )
})

export default Pay